.projects-section {
    padding: 60px 20px;
    background-color: #f4f4f4;
    text-align: center;
  }

  .projects-section h2 {
    font-size: 45px;
    color: #f1721e;
    margin-bottom: 40px;
    font-weight: 700;
    letter-spacing: 1px;
  }

  .projects-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  .project-card {
    background: #fff;
    border-radius: 10px;
    padding: 20px;
    width: 300px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .project-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }

  .project-card h3 {
    font-size: 22px;
    color: #007bff;
    margin-bottom: 10px;
  }

  .project-card p {
    font-size: 16px;
    color: #555;
    margin-bottom: 15px;
  }

  .project-link {
    font-size: 16px;
    color: #007bff;
    text-decoration: none;
    font-weight: 500;
  }

  .project-link:hover {
    text-decoration: underline;
  }

.footer {
    padding: 20px;
    background-color: #333;
    color: #fff;
    text-align: center;
    bottom: 0;
    width: 100%;
    font-size: 16px;
    position: fixed;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
  }

  .footer span {
    color: #e74c3c; 
  }